body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  height: 100%;
  width: 100%;
  margin: 0; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden; }

.App-header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
  flex-grow: 1; }
  @media (max-width: 599px) {
    .App-header {
      height: 100px; } }
  @media (min-width: 600px) {
    .App-header {
      height: 80px; } }
  @media (min-width: 900px) {
    .App-header {
      height: 100px; } }
  @media (min-width: 1200px) {
    .App-header {
      height: 100px; } }

.logoBox {
  padding: .5rem; }

.logoSvg {
  width: 300px; }

.logo {
  stroke-width: 1px;
  stroke: #fff;
  fill: none;
  font-family: sans-serif;
  font-size: calc(20px + .1vmin); }

.App-link {
  color: #61dafb; }

.List {
  display: flex;
  flex-direction: column;
  flex-basis: content;
  justify-content: center;
  flex-grow: 10;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  height: 80%;
  width: 100%;
  overflow-y: auto; }

.liDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #545a66;
  margin-left: calc(5px + 10vw);
  margin-right: calc(5px + 10vw);
  margin-bottom: 20px;
  padding: .5rem;
  border-radius: 4px;
  overflow: hidden; }

.liImgDiv {
  width: 70px;
  align-self: center;
  flex-grow: 0;
  opacity: 1; }

.liImgDiv img {
  height: 70px;
  -webkit-clip-path: circle(34px at center);
          clip-path: circle(34px at center); }

.li {
  text-align: left;
  font-size: calc(9px + 0.5vw);
  flex-grow: 4;
  z-index: 2;
  margin-left: 1em; }

a {
  color: inherit; }

a:link {
  text-decoration: none; }

a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:active {
  text-decoration: underline; }

.neon-flicker {
  -webkit-animation: neon-flicker 2s linear both;
          animation: neon-flicker 2s linear both; }

.fade-in {
  -webkit-animation: fade-in 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
          animation: fade-in 1s cubic-bezier(0.39, 0.575, 0.565, 1) both; }

